.footer {
    display: flex;
    background-color: beige;
    height: 50vh;
    justify-content: center;
}

.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 700px;
    border: 1px solid;
}

.iconContainer {
    display: flex;
    width: 300px;
    height: 60px;
    border: 1px solid;
    justify-content: space-between;
}
.icon{
    width: 60px;
    height: 60px;
}